@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html{
  background: #E7F2EB;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}
a{
  text-decoration: none;
}
.container{
   width: 100%; 
   max-width: 1050px;
   margin: 0 auto;
   padding:10px;
   min-height: 100vh;
   
}



.content{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items:center;
    align-content: center;
    min-height: 100vh;
}
.title{
    font-size:3rem;
    color: rgb(74, 74, 74);
    text-transform: uppercase;
    text-align: center;
}
.animation{
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.description{
    color: #6D6D6D;
    text-align:center;
    margin: 0;
    padding: 0 30px;
    font-size: 1.5rem;
}
.flyer{
    width: 100%;
    border-radius:15px;
}
.btn-primary{
    color:white;
    background: #059CDD;
    padding:5px 30px;
    border-radius:10px;
    margin:10px 0;
    font-size:2rem;
    
  }
.flyer-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.flyer-item-container{
    display: flex;
    flex-direction: column;
    margin: 10px;
}
.flyer-header{
    max-width: 300px;
    border-radius:15px 15px 0 0;
}
.flyer-text{
    max-width: 300px;
    height: 100px;
    text-align: start;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 10px;
    font-weight: bold;
    color: #6D6D6D;
}
.flyer-body{
    max-width: 300px;
    border-radius:0 0 15px 15px;
}
.flyer-yellow{
    background: #FFF7B1;
}
.flyer-blue{
    background:#D2DFF3;
}
.flyer-brown{
    background:#DAC7BE;
}
.flyer-green{
    background:#D0E2D1;
}
.flyer-grey{
    background:#D5DFE4;
}
@media (max-width: 750px){
    .animation{
        margin-top: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        padding-right: 50px;
    }
}


.side-container{
    background:#97ECAA;
    height:600px;
    width:300px;
    border-radius: 15px;
    display:flex;
    flex-direction:column;
    justify-content:space-around;
    align-items: center;

}
.description{
    font-size:1.2rem;
    background: #2D9F69;
    padding:10px;
    margin: 0 10px;
    border-radius:15px;
    color:#fff
}
.random-image{
    width: 80%;
    height: auto;
    padding:5px;
    border-radius: 15px;
    margin:0 auto;
}
.timer-countdown{
    color: #333;
    font-size:1.5rem;
}
.score{
    background:#01057A;
    color:#fff;
    padding:20px 30px;
    border-radius: 15px;
    width:80%;
    text-align:center
}
.btn{
    font-size:1.2rem;
    border:none
}
.content-center{
    display: flex;
    margin:0 auto;
    justify-content:center;
    align-self:center;
    align-items:center;
}
@media (max-width: 750px){
    .content-center{
        display: flex;
        flex-direction: column;
        margin:0 auto;
        justify-content:center;
        align-self:center;
        align-items:center;
    }
    .sidebar{
        max-width:600px;
    }
    .card{
        width: 100%;
        max-width: 100px;
        margin:10px;
        box-shadow: 4px 6px 5px 0px rgba(168,168,168,1);
        float: left;
        position: relative;
        top: 0;
        cursor: pointer;
        border: none;
        border-radius: 15px;
        /* Smoothing out hover using css3 transitions, if not, the pressed button effect looks a bit hard */
        transition: all 0.2s ease;
    
        /* Size */
        line-height: 45px;
        
        font-size: 120%;
    
        /* Colors */
        background: #f7f7f7;
        box-shadow: 0 6px rgba(0, 0, 0, 0.2);
        color: #888;
        
    }
    .card:hover{
        box-shadow: 0 10px #000;    
    
    }
    .card:active{
        top: 5px;
      box-shadow: none;
    }
}
.sidebar{
    max-width:300px;
}
.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.card{
    width: 100%;
    max-width: 300px;
    margin:10px;
    box-shadow: 4px 6px 5px 0px rgba(168,168,168,1);
    float: left;
    position: relative;
    top: 0;
    cursor: pointer;
    border: none;
    border-radius: 15px;
    transition: all 0.2s ease;
    line-height: 45px;
    font-size: 120%;
    background: #f7f7f7;
    box-shadow: 0 6px rgba(0, 0, 0, 0.2);
    color: #888;
    
}
.card:hover{
    box-shadow: 0 10px #000;    
}
.card:active{
    top: 5px;
    box-shadow: none;
}
.result-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    align-content: center;
}
.result-content{
    display: flex;
    margin: 25px 0;;
}

.result-image{
    max-width: 700px;
    margin: 0 auto;
    margin-right:30px;
}
.animation-result{
    max-width: 700px;
    margin: 0 auto;
    margin-right:30px;
}
.result-counters{
    display: flex;
    flex-direction: column;
}
.counter{
    color: #ffffff;
    width: 350px;
    border-radius: 15px;
    margin: 10px 0;
    font-size:2rem;
    padding:20px 10px;
}
.correct-counter{
    background: #72DA7D;
}
.wrong-counter{
    background: #DA7272;
}
.result-text{
    font-size:1.2rem;
    color:#DA7272;
    font-weight: bold;
    text-align: center;
}
.red{
    color:#DA7272;
}
.green{
    color:#007b0c;
}

@media (max-width: 750px){
    .result-content{
        display: flex;
        flex-direction: column;
        margin: 25px 0;
        justify-content:center;
        align-items: center;
        align-content: center;
    }
    .result-image{
        max-width: 300px;
        margin: 0 auto;
        margin-right:30px;
        padding:10px;
    }
    .animation-result{
        max-width: 400px;
        margin:0 auto;
    }
}
